// Cosas utiles

import axios from "axios";

export let ip = ''

if (window.location.hostname === "localhost" || /^(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])$/g.test(window.location.hostname) ) {
    ip = "http://127.0.0.1:8000";
    // ip = "http://192.168.1.50:9009";

} else {
    ip = window.location.protocol + "//" + window.location.hostname;
}

console.log('Conectado al ip: ' + ip)


const TOKEN_KEY = "TOKEN";

export function setToken(token) {
  sessionStorage.setItem(TOKEN_KEY, token);
}

export function setTokenStorage(token) {
  localStorage.setItem(TOKEN_KEY, token);
}

export function getToken() {
  return sessionStorage.getItem(TOKEN_KEY);
}

export function getTokenStorage() {
  return localStorage.getItem(TOKEN_KEY);
}

export function deleteToken(e) {
    e.preventDefault();
    sessionStorage.removeItem("TOKEN");
    localStorage.removeItem("TOKEN");
    window.location = "/";
}

export async function getCurrentUser() {
  if (!getToken() && !getTokenStorage()) {
    return;
  }
  try {
    let response = await axios.get(ip + "/auth/user");
    return response.data;
  } catch (error) {
    return false;
  }
}

export function initAxiosInterceptors() {
  axios.interceptors.request.use(config => {
    const token = getToken();
    const tokenStorage = getTokenStorage();

    if (token) {
      // console.log("entro token");
      
      config.headers.Authorization = `Bearer ${token}`;
    }
    if(tokenStorage) {
      // console.log("entro");
      console.log(tokenStorage);
      
      config.headers.Authorization = `Bearer ${tokenStorage}`;
    }
    return config;
  });

  axios.interceptors.response.use(
    response => {
      return response;
    },
    error => {
      if (error.response.status === 401) {
        sessionStorage.removeItem("TOKEN");
        localStorage.removeItem("TOKEN");
        window.location = "/";
      } else {
        return Promise.reject(error);
      }
    }
  );
}