import React, { Component } from 'react'
import { Link, Redirect } from 'react-router-dom'
import axios from 'axios'
import { ip } from './../../Utility'
import Swal from 'sweetalert2'

export class LoginForm extends Component {

    state = {
            redirect: false,

            email: '',
            username: '',
            password: '',
            password_confirmation: '',
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleSubmit = (e) => {

        e.preventDefault()

        const {email, username, password, password_confirmation} = this.state
        let data = {email, username, password, password_confirmation}

        axios.post(`${ip}/auth/signup`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Exito al registrarse',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

            this.setState({redirect : true}) 

            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }


    render() {

        const { redirect } = this.state

        if(redirect){
            return <Redirect to='/' />
        }

        return (
            <div className="limiter">
                <div className="container-login100">
                    <div className="wrap-yoyo">
                        <span className="login100-form-title">
                            Nuevo Usuario
                        </span>
                        
                        <form className="validate-form" onSubmit={this.handleSubmit}>

                            <div className="row">
                                <div className="col-md-6">
                                    <div className="wrap-input100 validate-input">
                                        <input className="input100" id="email" type="text" name="email" placeholder="Correo Electronico" onChange={this.handleChange} required />
                                        <span className="focus-input100"></span>
                                        <span className="symbol-input100">
                                            <i className="fas fa-envelope" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                                
                                <div className="col-md-6">
                                    <div className="wrap-input100 validate-input">
                                        <input className="input100" id="username" type="text" name="username" placeholder="Nombre de Usuario"  required onChange={this.handleChange} />
                                        <span className="focus-input100"></span>
                                        <span className="symbol-input100">
                                            <i className="fas fa-user-tie" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="wrap-input100 validate-input">
                                        <input className="input100" id="password" type="password" name="password" placeholder="Contraseña" required onChange={this.handleChange} />
                                        <span className="focus-input100"></span>
                                        <span className="symbol-input100">
                                            <i className="fa fa-lock" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="wrap-input100 validate-input">
                                        <input className="input100" id="password_confirmation" type="password" name="password_confirmation" placeholder="Repetir Contraseña" required onChange={this.handleChange} />
                                        <span className="focus-input100"></span>
                                        <span className="symbol-input100">
                                            <i className="fa fa-lock" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <div className="container-login100-form-btn pt-5">
                                <button className="login100-form-btn">
                                    Registrarse
                                </button>
                                <Link to='/' className="txt2 pt-4">
                                    Ya tengo cuenta
                                    <i className="fa fa-long-arrow-right m-l-5" aria-hidden="true"></i>
                                </Link>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default LoginForm
