import React from 'react'
import './login.css'
import './login-util.css' 
import { Link } from 'react-router-dom'
import axios from 'axios'
import { ip, setToken } from './../../Utility'
import Swal from 'sweetalert2'


export class Login extends React.Component {

    state = {
        email: '',
        password: ''
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleSubmit = async (e) => {

        e.preventDefault()

        const {email, password} = this.state
        let data = {email, password}

        await axios.post(`${ip}/auth/login`, data)
            .then(res => {

                setToken(res.data.access_token)

                window.location = '/home'

            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }


    render() {
        return (
            <div className="limiter">
                <div className="container-login100">
                    <div className="wrap-yoyo">
                        <div className="login100-pic js-tilt" data-tilt>
                            <img src="images/img-01.png" alt="IMG" />
                        </div>

                        <form onSubmit={this.handleSubmit} className="login100-form validate-form">
                            <span className="login100-form-title">
                                Josue Almacen
                            </span>

                            <div className="wrap-input100 validate-input" data-validate = "Valid email is required: ex@abc.xyz">
                                <input id="email" className="input100" type="text" name="email" placeholder="Correo Electronico" onChange={this.handleChange} required />
                                <span className="focus-input100"></span>
                                <span className="symbol-input100">
                                    <i className="fas fa-envelope" aria-hidden="true"></i>
                                </span>
                            </div>

                            <div className="wrap-input100 validate-input" data-validate = "Password is required">
                                <input id="password" className="input100" type="password" name="pass" placeholder="Contraseña" onChange={this.handleChange} required />
                                <span className="focus-input100"></span>
                                <span className="symbol-input100">
                                    <i className="fa fa-lock" aria-hidden="true"></i>
                                </span>
                            </div>
                            
                            <div className="container-login100-form-btn">
                                <button className="login100-form-btn">
                                    Iniciar Sesion
                                </button>
                            </div>

                            {/* <div className="text-center p-t-12">
                                <span className="txt1">
                                    Olvidó
                                </span>
                                <a className="txt2" href="#">
                                    &nbsp;Contraseña
                                </a>
                            </div> */}

                            <div className="text-center p-t-50">
                                <Link to='/new/account' className="txt2">
                                    Crear una nueva cuenta
                                    <i className="fa fa-long-arrow-right m-l-5" aria-hidden="true"></i>
                                </Link>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login


