import React, { Component } from 'react'
import Sidebar from '../Layouts/Sidebar/Sidebar'
import { Link, Redirect } from 'react-router-dom'
import { getCurrentUser } from './../../Utility'
import axios from 'axios'
import { ip } from './../../Utility'
import Swal from 'sweetalert2'


export class CardFolder extends Component {

    state = {
        user: {},

        folder: [],

        name: '',

        editSlug: '',
        editName: ''
    }

    handleUpdate = async (e) => {

        e.preventDefault()

        const {editName, editSlug} = this.state
        
        let data = {name: editName, slug: editSlug}

        await axios.post(`${ip}/folder/update`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Carpeta Actualizada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = `/home`
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }

    handleSetItemEdit = (e) => {

        let folder = this.state.folder[e.target.id]

        this.setState({
            editName: folder.name,
            editSlug: folder.slug
        })

        console.log(folder)
        console.log(this.state.editName)
    }

    obtaingFolder = async (e) =>{
        let folder = await axios.get(ip + "/folder")

        return folder.data
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleDelete = (e) => {

        let data = {
            slug: e.target.id
        }
        
        axios.post(`${ip}/folder/delete`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Carpeta Eliminada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = "/home"
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }

    handleSubmit = async (e) => {

        e.preventDefault()

        const {name} = this.state
        let data = {name}

        await axios.post(`${ip}/folder/create`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Carpeta Creada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = "/home"
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }

    async componentDidMount() {
        let user = await getCurrentUser()
        let folder = await this.obtaingFolder()

        this.setState({
            user,
            folder
        })
    }
    render() {

        const {  folder } = this.state

        if(folder.length === 0)
        {
            return (
                <React.Fragment>

                <Sidebar
                    user={this.state.user}
                />

                    <div className="container">
                        <div className="mx-auto pt-5">
                            <h2 className="text-center text-info">No tienes carpetas de credenciales, por favor cree una.</h2>
                        </div>
                    </div>

                    <div className="mx-auto text-center mt-5">
                        <button className="btn btn-success" data-toggle="modal" data-target="#create-folder">Nueva Carpeta</button>

                        {/* MODAL  */}
                        <div className="modal fade" id="create-folder" tabIndex="-1" role="dialog" aria-labelledby="create-folderLabel" aria-hidden="true">
                            <div className="modal-dialog" role="document">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="create-folderLabel">Nueva Carpeta</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <input type="text" id="name" className="form-control" placeholder="Nombre de la carpeta" required maxLength="35" name="name" value={this.state.name} onChange={this.handleChange} />
                                    </div>
                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                        <button type="button" onClick={this.handleSubmit} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            )
        }

        return (
            
            <React.Fragment>

            <Sidebar
                user={this.state.user}
            />

                <div className="container">
                    <div className="row">
                        {
                            this.state.folder.map((folder,i) => {
                                let url = `/credentials/${folder.slug}`
                                return (
                                    <div className="col-md-3 mt-4">
                                        <div className="card mb-4" style={{ width: '100%', alignItems:'center' }}>
                                            <img src="images/folder.png" style={{ width: '60%' }} className="card-img-top" alt="..."/>
                                            <div className="card-body text-center">
                                                <h5 className="card-title">{folder.name}</h5>
                                                <p className="card-text">Credenciales en carpeta: <span className="text-danger font-weight-bold">&nbsp;&nbsp;{folder.count_credentials}</span></p>
                                                <br/>
                                                <Link to={url} params={{ testvalue: "hello" }} className="text-primary card-link">Ver mas</Link>

                                                <a href="#" id={i} data-toggle="modal" onClick={this.handleSetItemEdit} data-target="#edit-folder" className="text-success card-link">Editar</a>                                                
                                                <a href="#" id={folder.slug} name="slug" onClick={this.handleDelete} className="text-danger card-link">Eliminar</a>
                                            </div>
                                        </div>
                                    </div>  
                                )
                            
                            })
                        
                        }

                    </div>
                </div>

                <div className="mx-auto text-center mt-5">
                    <button className="btn btn-success" data-toggle="modal" data-target="#create-folder">Nueva Carpeta</button>

                    {/* MODAL  */}
                    <div className="modal fade" id="create-folder" tabIndex="-1" role="dialog" aria-labelledby="create-folderLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="create-folderLabel">Nueva Carpeta</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <input type="text" id="name" className="form-control" placeholder="Nombre de la carpeta" required maxLength="35" name="name" value={this.state.name} onChange={this.handleChange} />
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                    <button type="button" onClick={this.handleSubmit} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* MODAL  */}
                    <div className="modal fade" id="edit-folder" tabIndex="-1" role="dialog" aria-labelledby="edit-folderLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="edit-folderLabel">Editar Carpeta</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <input type="text" id="editName" className="form-control" placeholder="Nombre de la carpeta" required maxLength="35" name="name" defaultValue={this.state.editName} onChange={this.handleChange} />
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                    <button type="button" onClick={this.handleUpdate} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default CardFolder
