import React, { Component } from 'react'
import Sidebar from '../Layouts/Sidebar/Sidebar'
import { Link } from 'react-router-dom'
import { ip } from './../../Utility'
import axios from 'axios'
import { getCurrentUser } from './../../Utility'
import Swal from 'sweetalert2'

export class Credential extends Component {

    state = {
        user: {},
        credentials : [],

        slug: '',
        url: '',
        name: '',
        pwCredential: '',

        editSlug: '',
        editName: '',
        editUrl: '',
        editCredential: '',

        view: false

    }

    copyInfo = (e) =>{
        let parts = e.target.id.split('çççç')
        let id = (parseInt(parts[0]))

        const {name, url, credential} = this.state.credentials[id]
        let creden = {name, url, credential}

        let message = `${name} ${url} ${credential}`

        // Del copiado
        try{
            let aux = document.createElement("input");
            aux.value = message

            document.body.appendChild(aux);
            aux.select();
            document.execCommand("copy");
            document.body.removeChild(aux);

            Swal.fire({
                type: 'success',
                title: 'Copiado',
                showCloseButton: true,
                showConfirmButton: false,
                timer:1000
            })
        }catch(e){
            Swal.fire({
                type: 'error',
                title: 'Error',
                showCloseButton: true,
                showConfirmButton: false,
                timer:1000
            })
        }


    }

    handleUpdate = async (e) => {

        e.preventDefault()

        const {editName, editUrl, editCredential, editSlug} = this.state
        
        let data = {name: editName, url: editUrl, pwCredential: editCredential, slug: editSlug}
        let slug = this.props.match.params.slug


        await axios.post(`${ip}/credentials/update`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Credencial Actualizada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = `/credentials/${slug}`
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }

    handleSetItemEdit = (e) => {

        let credential = this.state.credentials[e.target.id]

        this.setState({
            editSlug: credential.slug,
            editName: credential.name,
            editUrl: credential.url,
            editCredential: credential.credential,
        })
    }

    handleGenerator = (e) => {
        let b1 = Math.random().toString(36).substr(4,30);
        let b2 = Math.random().toString(36).substr(4,30);

        let pw = b1+b2

        document.getElementById("pwCredential").value = ""
        document.getElementById("pwCredential").value = pw

        this.setState({pwCredential: pw})
    }

    handleDelete = (e) => {

        let data = {
            slug: e.target.id
        }

        let slug = this.props.match.params.slug
        
        axios.post(`${ip}/credentials/delete`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Credencial Eliminada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = `/credentials/${slug}`
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });
    }

    showPassword = (e) => {
        
        let parts = e.target.id.split('çççç')
        
        let id = (parseInt(parts[0]) + 1)
        
        document.getElementById(id).value = true

        if(document.getElementById(id).value == true)
        {
            document.getElementById(id).innerHTML = parts[1]
        } 
        
    }

    hideOverPassword = (e) => {
        let parts = e.target.id.split('çççç')
        
        let id = (parseInt(parts[0]) + 1)
        
        document.getElementById(id).value = false

        if(document.getElementById(id).value == false)
        {
            document.getElementById(id).innerHTML = this.hidePassword(parts[1])
        } 
    }

    hidePassword = (e) => {
        let letter = ""

        for(let i = 0; i < e.length; i++)
        {
            letter = letter + "*"
        }

        return(letter)

    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleSubmit = async (e) => {

        e.preventDefault()

        const {name, url, pwCredential} = this.state
        let slug = this.props.match.params.slug
        
        let data = {name, url, pwCredential, slug}

        await axios.post(`${ip}/credentials/create`, data)
            .then(res => {
                Swal.fire({
                    type: 'success',
                    title: 'Credencial Creada',
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })

                setTimeout(() => {
                    window.location = `/credentials/${slug}`
                }, 3000)
            })
            .catch(error => {
                Swal.fire({
                    type: 'error',
                    title: 'Un Error a ocurrido: ',
                    text: error.response.data.errors[0],
                    showCloseButton: true,
                    showConfirmButton: false,
                    timer:3000
                })
            });

    }

    getCredential = async (e) =>{

        let credentials = await axios.get(`${ip}/credentials/${e}`)

        return credentials.data
    }

    async componentDidMount () {
        let credentials = await this.getCredential(this.props.match.params.slug)
        let user = await getCurrentUser()

        this.setState({ credentials, user })

    }

    render() {

        const { credentials , view} = this.state

        if(credentials.length === 0)
        {
            return(
                <div>
                    <div>
                        <Sidebar
                            user={this.state.user}
                        />
                    
                        <div className="container">
                            <div className="mx-auto pt-5">
                                <h2 className="text-center text-info">No tienes credenciales en esta carpeta.</h2>
                            </div>
                        </div>
                    </div>
                    
                    <div className="mx-auto text-center mt-5">
                        <Link to="/home">
                            <button className="btn btn-danger">Volver Atras</button>
                        </Link>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <button className="btn btn-success" data-toggle="modal" data-target="#create-folder">Nueva Credencial</button>
                    
                        
                        <div className="modal fade" id="create-folder" tabindex="-1" role="dialog" aria-labelledby="create-folderLabel" aria-hidden="true">
                            <div className="modal-dialog" role="document">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="create-folderLabel">Nueva Credencial</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <input type="text" id="name" placeholder="Descripcion" className="form-control" required maxlength="60" name="name" value={this.state.name} onChange={this.handleChange} />
                                    </div>
                                    <div className="modal-body">
                                        <input type="text" id="url" placeholder="URL" className="form-control" required maxlength="60" name="url" value={this.state.url} onChange={this.handleChange} />
                                    </div>
                                    <div className="modal-body">
                                        <input type="password" id="pwCredential" placeholder="Credencial" className="form-control" required maxlength="60" name="pwCredential" value={this.state.pwCredential} onChange={this.handleChange} />
                                        <button className="btn btn-warning mt-3 text-dark" onClick={this.handleGenerator}>!Generar Contrasena Segura!</button>
                                    </div>
                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                        <button type="button" onClick={this.handleSubmit} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }

        return (
            <React.Fragment>

                <Sidebar user={this.state.user} />

                <table className="table table-hover mt-4 text-center">
                    <thead className="thead-light">
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nombre</th>
                            <th scope="col">URL</th>
                            <th scope="col">Credencial</th>
                            <th scope="col">Acciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            credentials.map((folder, key) => {

                                return (
                                    
                                    <tr>
                                        <th scope="row">{key + 1}</th>
                                            <td>{folder.name}</td>
                                            <td><a href={folder.url} target="_blank">{folder.url}</a></td>
                                            <td id={key + 1} value={folder.view}>{this.hidePassword(folder.credential)}</td>
                                            <input type="hidden" id="copyHi" name="copyHi" />
                                            
                                            <td>
                                                <i class="fas fa-eye text-primary" perez="121212" data-toggle="tooltip" id={key + "çççç" + folder.credential} onClick={this.copyInfo} onMouseOver={this.showPassword} onMouseOut={this.hideOverPassword}  title="Mostrar Credencial">&nbsp;&nbsp;</i> 
                                                <i class="fas fa-edit text-success" id={key} onClick={this.handleSetItemEdit} data-target="#edit-folder" data-toggle="modal" title="Editar Credencial">&nbsp;&nbsp;</i> 
                                                <i class="fas fa-trash-alt text-danger" data-toggle="tooltip" id={folder.slug} onClick={this.handleDelete}  title="Eliminar Credencial">&nbsp;&nbsp;</i>
                                            </td>
                                    </tr>

                             

                                )
                            })

                        }
                        <div className="modal fade" id="edit-folder" tabIndex="-1" role="dialog" aria-labelledby="edit-folderLabel" aria-hidden="true">
                            <div className="modal-dialog" role="document">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="edit-folderLabel">Editar Credencial</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <input type="text" id="editName" placeholder="Descripcion" className="form-control" required maxlength="60" name="editName" defaultValue={this.state.editName} onChange={this.handleChange} />
                                    </div>
                                    <div className="modal-body">
                                        <input type="text" id="editUrl" placeholder="URL" className="form-control" required maxlength="60" name="editUrl" defaultValue={this.state.editUrl} onChange={this.handleChange} />
                                    </div>
                                    <div className="modal-body">
                                        <input type="password" id="editCredential" placeholder="Credencial" className="form-control" required maxlength="60" name="editCredential" defaultValue={this.state.editCredential} onChange={this.handleChange} />
                                        <button className="btn btn-warning mt-3 text-dark" onClick={this.handleGenerator}>!Generar Contrasena Segura!</button>
                                    </div>
                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                        <button type="button" onClick={this.handleUpdate} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                    </div>
                                   
                                </div>
                            </div>
                        </div>
                    </tbody>
                </table>

                <div className="mx-auto text-center mt-5">
                    <Link to="/home">
                        <button className="btn btn-danger">Volver Atras</button>
                    </Link>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <button className="btn btn-success" data-toggle="modal" data-target="#create-folder">Nueva Credencial</button>
                        
                    <div className="modal fade" id="create-folder" tabindex="-1" role="dialog" aria-labelledby="create-folderLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="create-folderLabel">Nueva Credencial</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <input type="text" id="name" placeholder="Descripcion" className="form-control" required maxlength="60" name="name" value={this.state.name} onChange={this.handleChange} />
                                </div>
                                <div className="modal-body">
                                    <input type="text" id="url" placeholder="URL" className="form-control" required maxlength="60" name="url" value={this.state.url} onChange={this.handleChange} />
                                </div>
                                <div className="modal-body">
                                    <input type="password" id="pwCredential" placeholder="Contrasena" className="form-control" required maxlength="60" name="pwCredential" value={this.state.pwCredential} onChange={this.handleChange} />
                                    <button className="btn btn-warning mt-3 text-dark" onClick={this.handleGenerator}>!Generar Contrasena Segura!</button>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                    <button type="button" onClick={this.handleSubmit} className="btn btn-primary" data-dismiss="modal" aria-label="Close">Crear</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </React.Fragment>
        )
    }
}

export default Credential
